.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}



.card {
  width: 98.8vw;
  margin: auto;
  height: 100vh;
  background: rgba(0, 0, 255, 0.275);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  position: sticky;
  top: 0;
}

.card img {
  position: absolute;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;

}

.card-content {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}



.aminitiescontainer {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  overflow: hidden;
  height: 15rem;
}

.forthpgcont {
  width: 58vw;
}

.immcon {

  width: 15rem;
  height: 15rem;
  margin: 0.5rem;
  overflow: hidden;
  border-radius: 1rem;
}

.immcon2 {

  width: 15rem;
  height: 15rem;
  margin: 0.5rem;
  overflow: hidden;
  border-radius: 1rem;
}

.forthpgcont img:hover {
  transform: scale(1.3);
  transition: transform 1.5s;
}

.forthpgcont2 img:hover {
  transform: scale(1.3);

  transition: transform 1.5s;
}

.forthpgcont img {
  border-radius: 1rem;


}

.forthpgcont2 {
  width: 50vw;

}


.forthpgcont2 img {
  border-radius: 1rem;
  margin: 0.5rem;
  width: 15rem;
  height: 15rem;

}

.hcimgoutercont {
  padding: 1rem;
  width: 90%;
  margin: auto;
  overflow: hidden;
  margin-top: 2.5rem;
  background-color: #1d1919;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 13rem;
  border-radius: 1rem;
}

.hcitext {
  font-size: 1.5rem;
  font-family: cursive;
}

.hcimgoutercont img:hover {
  transform: scale(1.1);
  transition: transform 1.5s;
}

.hcimgoutercont img {
  position: relative;
  width: 40rem;
  height: 8rem;
  border-radius: 1rem;
}

@media screen and (max-width: 768px) {
  .aminitiescontainer {
    grid-template-columns: repeat(2, 1fr);
    overflow-y: scroll;
  }
}


@media screen and (max-width: 480px) {
  .forthpgcont {
    width: 90vw;
    height: 70vh;
    overflow: scroll;
  }

  .forthpgcont img {
    border-radius: 1rem;
    margin: 0.5rem;
  }

  .forthpgcont2 {
    width: 90vw;
    height: 70vh;
    overflow: scroll;
  }

  .immcon {
    border-radius: 1rem;
    margin: 0.5rem;
    width: 15rem;
    height: 15rem;
    overflow: hidden;
  }

  .immcon2 {
    border-radius: 1rem;
    margin: 0.5rem;
    width: 15rem;
    height: 15rem;
    overflow: hidden;
  }



  .aminitiescontainer {
    grid-template-columns: repeat(2, 1fr);
    overflow-y: scroll;
    height: 21rem;
  }


  .hcimgoutercont {
    width: 100%;

    background-color: #1d1919;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 13rem;
    border-radius: 1rem;
  }

  .hcitext {
    font-size: 1.5rem;
    font-family: cursive;
  }

  .hcimgoutercont img {
    position: relative;
    width: 95vw;
    height: 8rem;
    object-fit: contain;
    border-radius: 1rem;
  }

  .hcimgoutercont img:hover {}
}



.type_writer {
  display: inline-block;
  width: 20rem;
}

.type_writer p {
  font-size: 1.5rem;
  letter-spacing: 0.3rem;
  font-family: sans-serif;
  border-right: 0.1rem solid;
  width: 100%;
  height: 15rem;
  overflow: hidden;
  animation: typing 2s,
    cursor .4s step-end 5 alternate;
}


@keyframes cursor {
  50% {
    border-color: transparent;
  }
}

@keyframes typing {
  from {
    width: 0;
  }
}



